@tailwind base;
@tailwind components;
@tailwind utilities;

@layer base {
  :root {
    --background: 222 47% 11%;
    --foreground: 210 40% 98%;
    --card: 222 47% 11%;
    --card-foreground: 210 40% 98%;
    --popover: 222 47% 11%;
    --popover-foreground: 210 40% 98%;
    --primary: 217 91% 60%;
    --primary-foreground: 210 40% 98%;
    --secondary: 199 89% 48%;
    --secondary-foreground: 210 40% 98%;
    --muted: 217 32% 17%;
    --muted-foreground: 215 20% 65%;
    --accent: 316 70% 43%;
    --accent-foreground: 210 40% 98%;
    --destructive: 0 84% 60%;
    --destructive-foreground: 210 40% 98%;
    --border: 217 32% 17%;
    --input: 217 32% 17%;
    --ring: 224 76% 48%;
    --radius: 0.5rem;
  }

  .dark {
    --background: 222.2 84% 4.9%;
    --foreground: 210 40% 98%;
    --card: 222.2 84% 4.9%;
    --card-foreground: 210 40% 98%;
    --popover: 222.2 84% 4.9%;
    --popover-foreground: 210 40% 98%;
    --primary: 217.2 91.2% 59.8%;
    --primary-foreground: 222.2 47.4% 11.2%;
    --secondary: 217.2 32.6% 17.5%;
    --secondary-foreground: 210 40% 98%;
    --muted: 217.2 32.6% 17.5%;
    --muted-foreground: 215 20.2% 65.1%;
    --accent: 217.2 32.6% 17.5%;
    --accent-foreground: 210 40% 98%;
    --destructive: 0 62.8% 30.6%;
    --destructive-foreground: 210 40% 98%;
    --border: 217.2 32.6% 17.5%;
    --input: 217.2 32.6% 17.5%;
    --ring: 224.3 76.3% 48%;
  }
}

@layer base {
  * {
    @apply border-border;
  }
  body {
    @apply bg-background text-foreground;
    font-feature-settings: "rlig" 1, "calt" 1;
  }
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.skill-tag {
  @apply px-3 py-1 rounded-full text-sm font-medium bg-muted/50 text-primary/90 border border-primary/20 transition-all hover:border-primary/40 hover:bg-muted/70;
}

.experience-card {
  @apply p-6 rounded-lg border border-border/50 transition-all hover:border-primary/30;
  background: linear-gradient(
    135deg,
    rgba(var(--muted) / 0.5),
    rgba(var(--card) / 0.5)
  );
  backdrop-filter: blur(12px);
}

.section-title {
  @apply text-4xl font-bold mb-8 bg-clip-text text-transparent;
  background: linear-gradient(to right, rgb(var(--primary)), rgb(var(--accent)));
}

.nav-link {
  @apply relative text-lg font-medium text-foreground/80 transition-colors hover:text-primary;
}

.nav-link::after {
  content: '';
  @apply absolute left-0 -bottom-1 w-0 h-0.5 bg-primary transition-all duration-300;
}

.nav-link:hover::after {
  @apply w-full;
}

.hero-gradient {
  background: 
    linear-gradient(to bottom, rgba(var(--background) / 0.9), rgba(var(--background) / 1)),
    radial-gradient(circle at 20% 30%, rgba(var(--primary) / 0.15) 0%, transparent 70%),
    radial-gradient(circle at 80% 70%, rgba(var(--accent) / 0.15) 0%, transparent 70%);
}

.glow {
  box-shadow: 0 0 20px rgba(var(--primary) / 0.2);
}

.timeline-dot::before {
  content: '';
  @apply absolute -left-3 w-6 h-6 rounded-full bg-muted border-2 border-primary/50;
}

.timeline-line::after {
  content: '';
  @apply absolute left-0 top-6 w-0.5 h-full bg-primary/20;
}

@keyframes float {
  0%, 100% { transform: translateY(0px); }
  50% { transform: translateY(-10px); }
}

.float-animation {
  animation: float 3s ease-in-out infinite;
}

@keyframes glow {
  0%, 100% { box-shadow: 0 0 20px rgba(var(--primary) / 0.2); }
  50% { box-shadow: 0 0 30px rgba(var(--primary) / 0.4); }
}

.glow-animation {
  animation: glow 3s ease-in-out infinite;
}

.glass-effect {
  backdrop-filter: blur(12px);
  background: rgba(var(--background) / 0.8);
  border-bottom: 1px solid rgba(var(--primary) / 0.1);
}

.profile-image {
  @apply w-48 h-48 rounded-full shadow-xl mb-8;
  background: linear-gradient(135deg, rgba(var(--primary) / 0.8), rgba(var(--accent) / 0.8));
  padding: 4px;
}

.profile-image img {
  @apply w-full h-full rounded-full object-cover;
  background: rgb(var(--card));
}

.project-card {
  @apply p-6 rounded-lg border border-border/50 transition-all;
  background: linear-gradient(
    135deg,
    rgba(var(--muted) / 0.5),
    rgba(var(--card) / 0.5)
  );
  backdrop-filter: blur(12px);
}

.project-card:hover {
  @apply border-primary/30 shadow-lg;
  box-shadow: 0 8px 30px rgba(var(--primary) / 0.1);
  background: linear-gradient(
    135deg,
    rgba(var(--muted) / 0.6),
    rgba(var(--card) / 0.6)
  );
}

.project-icon {
  @apply w-12 h-12 rounded-lg flex items-center justify-center text-primary;
  background: linear-gradient(
    135deg,
    rgba(var(--primary) / 0.2),
    rgba(var(--primary) / 0.1)
  );
  border: 1px solid rgba(var(--primary) / 0.2);
}
